const TitleStyled = styled.h1`
  text-align: center;
  margin-top: 40px;
  color: #cbb5e7de;
  font-size: 65px;
  text-shadow: -6px 2px 9px #c581f999;
  transition: ease-in-out 1s;
  &:hover {
    color: #df8282;
    text-shadow: 1px 1px 12px #c35a5ad1;
  }
`;

const Game = () => {
  const [values, setValues] = useState(new Array(9).fill(null));
  const [player, setPlayer] = useState("X");
  const [winner, setWinner] = useState(false);
  const [player1, setPlayer1] = useState("Player X");
  const [player2, setPlayer2] = useState("Player O");
  const [counterX, setCounterX] = useState(0);
  const [counterO, setCounterO] = useState(0);
  return (
    <>
      <TitleStyled>Tic tac toe game</TitleStyled>
      <Board
        values={values}
        setValues={setValues}
        player={player}
        setPlayer={setPlayer}
        winner={winner}
        setWinner={setWinner}
        player1={player1}
        setPlayer1={setPlayer1}
        player2={player2}
        setPlayer2={setPlayer2}
        counterO={counterO}
        setCounterO={setCounterO}
        counterX={counterX}
        setCounterX={setCounterX}
      />
      <RefreshButton
        onSwitchPlayers={() => {
          setValues(new Array(9).fill(null));
          // setSwitchPlayers(player1);
          setPlayer1(player2);
          setPlayer2(player1);
          // setSwitchCounter(counterX);
          setCounterX(counterO);
          setCounterO(counterX);
          setPlayer("X");
          setWinner(false);
          setValues(new Array(9).fill(null));
        }}
        onClear={() => {
          setValues(new Array(9).fill(null));
          setPlayer("X");
          setWinner(false);
          setCounterX(0);
          setCounterO(0);
        }}
      />
    </>
  );
};

export default Game;
